// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.director.getScene().getChildByName('radio').active = false;
        cc.director.getScene().getChildByName('dialog1').active = false;
        cc.director.getScene().getChildByName('photo').active = false;

    },

    start () {

    },

    update (dt) {
        var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
        var Pos_attendance = cc.director.getScene().getChildByName('attendance').getPosition();
        var Pos_label = cc.director.getScene().getChildByName('label').getPosition();
        var Pos_photo = cc.director.getScene().getChildByName('photoonwall').getPosition();
        if (Pos_player.x <= Pos_attendance.x + 50 && Pos_player.x >= Pos_attendance.x - 50 && Pos_player.y <= Pos_attendance.y + 200 && Pos_player.y >= Pos_attendance.y - 200){
            cc.director.getScene().getChildByName('picofattendance').active = true;
        }
        else if (Pos_player.x <= Pos_label.x + 50 && Pos_player.x >= Pos_label.x - 50 && Pos_player.y <= Pos_label.y + 200 && Pos_player.y >= Pos_label.y - 200){
            cc.director.getScene().getChildByName('dialog1').active = true;
        }
        else if (Pos_player.x <= Pos_photo.x + 50 && Pos_player.x >= Pos_photo.x - 50 && Pos_player.y <= Pos_photo.y + 200 && Pos_player.y >= Pos_photo.y - 200){
            cc.director.getScene().getChildByName('photo').active = true;
        }
        else {
            cc.director.getScene().getChildByName('picofattendance').active = false;
            cc.director.getScene().getChildByName('photo').active = false;
            // cc.director.getScene().getChildByName('dialog1').active = false;
        }
    },
});
