// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},
    openRadio(){
        var player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player');
        var radio = cc.director.getScene().getChildByName('radio');
        if(cc.director.getScene().name == "scene1"){
            var items = cc.director.getScene().getChildByName('items').children;
            var name = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father').children[0].name;
            if(name == "diary3" && items.length == 1){
                cc.director.getScene().getChildByName('dialog3').active = true;
            }
            if(radio.active == true){
                cc.director.getScene().getChildByName('dialog1').active = false;
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }
        else if(cc.director.getScene().name == "scene2"){
            var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("diary_father");
            var radio = cc.director.getScene().getChildByName('radio');
            if(radio.active == true){
                if(diary_father.children.name == "diary3"){cc.director.getScene().getChildByName('dialog2').active = true;}
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }
        else if(cc.director.getScene().name == "scene3"){
            var radio = cc.director.getScene().getChildByName('radio');
            if(radio.active == true){
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }
        else if(cc.director.getScene().name == "control"){
            var radio = cc.director.getScene().getChildByName('radio');
            if(radio.active == true){
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }else if(cc.director.getScene().name == "electric"){
            var radio = cc.director.getScene().getChildByName('radio');
            if(radio.active == true){
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }
        else if(cc.director.getScene().name == "scene4"){
            var radio = cc.director.getScene().getChildByName('radio');
            if(radio.active == true){
                player.active = true;
                radio.active = false;
                cc.audioEngine.setEffectsVolume(0);
            }
            else if(radio.active == false){
                player.active = false;
                radio.active = true;
                cc.audioEngine.setEffectsVolume(1);
            }
        }


    },

    start () {

    },

    // update (dt) {},
});
