// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html
var flag = 0;
cc.Class({
    extends: cc.Component,

    properties: {
        player:dragonBones.ArmatureDisplay,
    },


    onLoad () {
        this.dir = cc.v2(0,0);
        this.player.timeScale = 3.8;
        this.player.playAnimation("向左站立");
        this.node.getChildByName("UI").getChildByName("joystick").on(cc.Node.EventType.TOUCH_MOVE,function(event){
            if(!this.player.node.getComponent(cc.AudioSource).isPlaying){this.player.node.getComponent(cc.AudioSource).play();}
            var w_pos = event.getLocation();
            var pos = this.node.convertToNodeSpaceAR(w_pos);//将世界坐标转化为父节点的相对坐标
            if(pos.x>150 && this.player.animationName !== "向右行走"){this.player.playAnimation("向右行走");flag = 1;}
            // if(pos.x>=100 && this.player.animationName !== "walk_left"){this.player.playAnimation("walk_left");}
            if(pos.x<150 && this.player.animationName !== "向左行走") {this.player.playAnimation("向左行走");flag = 0;}
            else return ;
        },this);

        this.node.getChildByName("UI").getChildByName("joystick").on(cc.Node.EventType.TOUCH_CANCEL,function(event){
            this.player.node.getComponent(cc.AudioSource).stop();
            if(flag == 0){this.player.playAnimation("向左站立");}
            if(flag == 1){this.player.playAnimation("向右站立");}
            else return;
            // console.log("cancel");
        },this);

        this.node.getChildByName("UI").getChildByName("joystick").on(cc.Node.EventType.TOUCH_END,function(event){
            this.player.node.getComponent(cc.AudioSource).stop();
            if(flag == 0){this.player.playAnimation("向左站立");}
            if(flag == 1){this.player.playAnimation("向右站立");}
            else return;
            // console.log("end");
        },this);
   },

    start () {

    },
    
});
