
cc.Class({
    extends: cc.Component,

    properties: {
      light:cc.Sprite,
      //  targetLight:cc.Sprite,
      previous:cc.Node,
      control:cc.Node,
    },
    
    onLoad(){

        this.node.on('touchmove',this.touchMove,this);
        this.node.on('touchend',this.touchEnd,this);

    },

    onDestroy(){

        this.node.off('touchmove',this.touchMove,this);         
        this.node.off('touchend',this.touchEnd,this);

    }, 
  
    touchMove(e){
        // cc.log(this.bounceTarget.x);
        let delta = e.getDelta();
        this.node.x += delta.x;       
         
      
          if(this.node.x <= -30){
              this.node.x = -30;
                // self.light.node.opacity=0;
                // this.bounceTarget.x = 100;
                

                
      }else if(this.node.x >=100){
          this.node.x = 100  ; 
                // self.light.node.opacity=255;
               // this.bounceTarget.x = -30;
                // self.targetLight.node.opacity =0;
                 if(this.previous.x!=100){
                  // cc.log("reset");
                 this.control.getComponent('order').reset() ;}

                 };

    },

    touchEnd(e){

      if( this.previous.x != 100){  
        // cc.log("reset");
        this.control.getComponent('order').reset() ;    }
      },

    update(dt){
      var self = this;
      if(   this.node.x == 100){
        self.light.node.opacity=255;
      }else{self.light.node.opacity=0}
    }
});
