// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        number:cc.String,
        label:cc.Label,
        panel:cc.Node,
    },

   

    input(){
        var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('newspaper_father');
      this.label.string =  this.label.string + this.number;
          /*按键声 */
        cc.director.getScene().getChildByName('panel').getChildByName("点击").getComponent(cc.AudioSource).play();
      if(this.label.string == "5467"){
        /*密码正确提示声 */
          cc.director.getScene().getChildByName('panel').getChildByName("close").getComponent(cc.AudioSource).play();
        /* exit */
          cc.director.loadScene('scene4');
          if(newspaper_father.children[0].name == "newspaper1"){
              newspaper_father.getChildByName("newspaper1").destroy();
              cc.director.getScene().getChildByName('newspaper2').active = true;
              cc.director.getScene().getChildByName('newspaper2').parent = newspaper_father;
          }
      }
      if(this.label.string.length > 5){
        this.label.string = ""
        cc.director.getScene().getChildByName("panel").active = false;
        // cc.log( cc.director.getScene().getChildByName("panel").active);
      /* 错误提示声 */
          cc.director.getScene().getChildByName('panel').getChildByName("错误").getComponent(cc.AudioSource).play();
      }
    },
    
    
    

});
