// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        // cc.director.getScene().getChildByName('panel').active = false;
        cc.director.getScene().getChildByName('dialog').active = true;
        cc.director.getScene().getChildByName('radio').active = false;
        cc.director.getScene().getChildByName('dialog1').active = false;
        cc.director.getScene().getChildByName('纸条').active = false;
        cc.director.getScene().getChildByName('picofaware').active = false;
    },

    start () {

    },

     update (dt) {
        var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
        var Pos_aware =  cc.director.getScene().getChildByName('aware').getPosition();
        var Pos_exit = cc.director.getScene().getChildByName('exit').getPosition();
        var Pos_item = this.node.getPosition();
         var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
         var pages = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father').children.length;
         if(Pos_player.x <= Pos_item.x + 200 && Pos_player.x >= Pos_item.x - 200 && Pos_player.y <= Pos_item.y + 50 && Pos_player.y >= Pos_item.y - 50 ){
             if(diary_father.children[0].name == "diary3"){
                 cc.loader.loadRes("passwordInGround", function (err, prefab) {
                     var passwordInGround = cc.instantiate(prefab);
                     cc.director.getScene().getChildByName('items').addChild(passwordInGround);
                 });
                 diary_father.getChildByName("diary3").destroy();
                 cc.director.getScene().getChildByName('diary4').active = true;
                 cc.director.getScene().getChildByName('diary4').parent = diary_father;
             }
             else cc.director.getScene().getChildByName("panel").active = true;
         }
         else if (Pos_player.x <= Pos_aware.x + 50 && Pos_player.x >= Pos_aware.x - 50 && Pos_player.y <= Pos_aware.y + 50 && Pos_player.y >= Pos_aware.y - 50){
             cc.director.getScene().getChildByName('picofaware').active = true;
         }
         else {cc.director.getScene().getChildByName("panel").active = false;
             cc.director.getScene().getChildByName("picofaware").active = false;}
     },
});
