// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
       bar:cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},
    bag(){
        if(this.bar.parent.active){
            this.bar.parent.active = false;
            if(cc.director.getScene().getChildByName('radio').active = true){
                cc.director.getScene().getChildByName('radio').active = false;
                cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').active = true;
                cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor').active = true;
                cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('joystick').active = true;
                cc.audioEngine.stopAll();
            }
        }
        else this.bar.parent.active = true;
    },
    sound(){
        if(this.bar.parent.active){
            this.node.getChildByName("拉链拉上").getComponent(cc.AudioSource).play();
        }
        else this.node.getChildByName("拉链拉开").getComponent(cc.AudioSource).play();
    },
    start () {

    },

    // update (dt) {},
});
