cc.Class({
    extends: cc.Component,

    properties: {
        channel:cc.Label,
        target:cc.Integer,
        radioFM1: {
            type: cc.AudioClip,
            default: null,
        },
        radioFM2: {
            type: cc.AudioClip,
            default: null,
        },
        radioFM3: {
            type: cc.AudioClip,
            default: null,
        },

    },

    onLoad(){
        this.channel.string =49.1;
        this.jude = this.radioFM1;
        this.node.on('touchstart',this.touchStart,this);
        this.node.on('touchmove',this.touchMove,this);
        this.node.on('touchend',this.touchEnd,this);
        cc.audioEngine.playEffect(this.radioFM1, false);
    },

    onDestroy(){
        this.node.off('touchstart',this.touchStart,this);
        this.node.off('touchmove',this.touchMove,this);
        this.node.off('touchend',this.touchEnd,this);
        // cc.audioEngine.pauseAllEffects();

    },
    touchMove(e){
        let delta = e.getDelta();

        this.node.x += delta.x;

        this.number=0;
        this.number=parseInt(this.node.x-74);


        this.node.x += delta.x;

        this.number=0;
        this.number=parseInt(this.node.x-74);

        this.channel.string = this.output;
        if(this.sound != this.jude){
            cc.audioEngine.stopAll();
            if(this.sound == this.radioFM3){
                if(cc.director.getScene().name == "scene1"){
                    cc.director.getScene().getChildByName('dialog1').active = false;
                    cc.director.getScene().getChildByName('dialog2').active = true;
                    var diaryfather = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
                    var name = diaryfather.children[0].name;
                    if(name == "diary2"){
                        diaryfather.getChildByName("diary2").destroy();
                        cc.director.getScene().getChildByName('diary3').active = true;
                        cc.director.getScene().getChildByName('diary3').parent = diaryfather;
                    }
                }
                else if(cc.director.getScene().name == "scene2"){
                    // cc.director.getScene().getChildByName('dialog2').active = true;
                    cc.director.getScene().getChildByName('dialog1').active = false;
                }
            }
            else
            {
                cc.director.getScene().getChildByName('dialog2').active = false;
                cc.director.getScene().getChildByName('dialog1').active = false;
            }
            this.playMusic();
            this.jude = this.sound;
        }
    },

    update(dt){

        if(this.node.x <= 74){this.node.x = 74
        }else if(this.node.x >=600){this.node.x = 600 };
        if(this.number<=0){this.number = 0
        }else if (this.number >=529){this.number = 529};
        this.output=49.1+ this.number * (167.6/529);
        this.output = this.output.toFixed(1);
        if(this.output>=this.target-0.2 && this.output<= this.target +0.2 ){
            this.sound = this.radioFM3;
        }else if (this.output <this.target-0.2){
            this.sound = this.radioFM1;
        }else if(this.output >this.target+0.2){
            this.sound = this.radioFM2;
        }
    },




    playMusic(){
        cc.audioEngine.playEffect(this.sound, false);
    },


});
/*美国FM87.5~107.9MHz
366+274=640    0-640  640
87.5-107.9=-107.9*/
