// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,
    properties: {
        view:cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.view.parent.active = false;
    },
diary(){
        if(this.view.parent.active){
            this.view.parent.active = false;
        }
        else {
            this.view.parent.active = true;
        }
},

    start () {

    },

    // update (dt) {},
});
