cc.Class({

    extends: cc.Component,

    properties: {

        itemBar:cc.Node,
        player: {

            default: null,

            type: cc.Node

        },

    },

    onLoad: function () {
        cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('photo').active = false;
        cc.director.getScene().getChildByName('radio').active = false;
        cc.director.getScene().getChildByName('dialog1').active = false;
        cc.director.getScene().getChildByName('dialog2').active = false;
        cc.director.getScene().getChildByName('dialog3').active = false;
        cc.director.getScene().getChildByName('dialog4').active = false;
        this.itemBar.active = false;
        this.camera = this.getComponent(cc.Camera);

    },

    // lateUpdate: function () {
    //
    //     // let targetPos = this.target.convertToWorldSpaceAR(cc.v2(0,0));
    //
    //     if(this.player == null){console.log("Wohoo Studio!");}
    //     else {
    //         let targetPos = this.player.getPosition();
    //         this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
    //     }
    // },

    update(){
        let targetPos = this.player.getPosition();
        // cc.log(targetPos);
        if(cc.director.getScene().name == "scene1"){return;}
        if(cc.director.getScene().name == "scene2"||cc.director.getScene().name == "scene22"){
            if(targetPos.x<=-996){targetPos.x = -996;}
            if(targetPos.y<331){targetPos.y = 331;}
            if(targetPos.x>=870){targetPos.x = 870;}
            if(targetPos.y>510){targetPos.y = 510;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }
        if(cc.director.getScene().name == "scene3"){
            if(targetPos.x<=-2850){targetPos.x = -2850;}
            if(targetPos.y<251){targetPos.y = 251;}
            if(targetPos.x>=-969){targetPos.x = -969;}
            if(targetPos.y>700){targetPos.y = 700;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }
        if(cc.director.getScene().name == "scene4"){
            if(targetPos.x<=-4376){targetPos.x = -4376;}
            if(targetPos.y<356){targetPos.y = 356;}
            if(targetPos.x>=-1846){targetPos.x = -1846;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }
        if(cc.director.getScene().name == "electric"){
            if(targetPos.x<=-4108){targetPos.x = -4108;}
            if(targetPos.y<361){targetPos.y = 361;}
            if(targetPos.x>=-3868){targetPos.x = -3868;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }
        if(cc.director.getScene().name == "control"){
            if(targetPos.x<=-2199){targetPos.x = -2199;}
            if(targetPos.y<360){targetPos.y = 360;}
            if(targetPos.x>=-1638){targetPos.x = -1638;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }
        if(cc.director.getScene().name == "final"){
            if(targetPos.x<=-2502){targetPos.x = -2502;}
            if(targetPos.y<360){targetPos.y = 360;}
            if(targetPos.x>=9103){targetPos.x = 9103;}
            this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
        }

        else this.node.position = this.node.parent.convertToNodeSpaceAR(targetPos);
    }
});
