cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.director.getScene().getChildByName('contentofnote').active = false;
        cc.director.getScene().getChildByName('circuit').active = false;
        cc.director.getScene().getChildByName('radio').active = false;
        cc.director.getScene().getChildByName('map').active = false;
    },

    start () {

    },

    // setTimeout(function () {
    //     this.target.destroy();
    // }.bind(this), 5000);

    update (dt) {
        var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
        var name = diary_father.children[0].name;
        var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor').getComponent(cc.Button);
            var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
            var Pos_note =  cc.director.getScene().getChildByName('note').getPosition();
            var Pos_circuit =  cc.director.getScene().getChildByName('circuit1').getPosition();
            var Pos_map =  cc.director.getScene().getChildByName('map1').getPosition();
            if (Pos_player.x <= Pos_circuit.x + 100 && Pos_player.x >= Pos_circuit.x - 100 && Pos_player.y <= Pos_circuit.y + 100 && Pos_player.y >= Pos_circuit.y - 100){
                inter.interactable = true;
            }
            else if (Pos_player.x <= Pos_note.x + 100 && Pos_player.x >= Pos_note.x - 100 && Pos_player.y <= Pos_note.y + 100 && Pos_player.y >= Pos_note.y - 100){
                cc.director.getScene().getChildByName('contentofnote').active = true;
            }
            else if (name == "diary7" && Pos_player.x <= Pos_map.x + 100 && Pos_player.x >= Pos_map.x - 100 && Pos_player.y <= Pos_map.y + 100 && Pos_player.y >= Pos_map.y - 100){
                cc.director.getScene().getChildByName('map').active = true;
            }
            else{
                cc.director.getScene().getChildByName('circuit').active = false;
                cc.director.getScene().getChildByName('map').active = false;
                cc.director.getScene().getChildByName('contentofnote').active = false;
                inter.interactable = false;
            }
    },
});
