//  - https://docs.cocos.com/creator/manual/en/scripting/class.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html
var node = 0;
cc.Class({
    extends: cc.Component,

    properties: {
        bottom:cc.Node,
        content:cc.Node,
        item : cc.Prefab,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.log(this.content);
    },
    getOn:function(contact,selfCollider){

    },

    onBeginContact: function (contact, selfCollider, otherCollider) {
        node = selfCollider.node;
        // cc.log("hit",node);
        this.bottom.getComponent(cc.Button).interactable = true;

        var items = cc.instantiate(this.item);

        cc.log(items,this.content);

        this.content.addChild(items);

        node.destroy();
    },

    onEndContact: function (contact, selfCollider, otherCollider) {
        this.bottom.getComponent(cc.Button).interactable = false;
        cc.log("leave");
    },

    start () {
    },

    update (dt) {
    },
});
