let roleMap = {/*人物名称和头像*/
    1:{
        name: "  ",
        url: "dialog/boi"
    },
};

cc.Class({
    extends: cc.Component,

    properties: {
        icon:cc.Sprite,
        nameLable:cc.Label,
        textLable:cc.Label,
        dialog:cc.Node,
        jude:cc.Integer,
        // player:cc.Node,
        // joyStick:cc.Node,
        // inter:cc.Node,
    },
    /*如果要调用dialog 在button里调用active 
    可以在角色移动的脚本上设置如果window.dialog.active = ture 就停止移动*/
    active(){        this.dialog.active = true;
    },

    start() {
        /*jude 是用来控制文本的，不同文本就要绑定不同的dialog的Node，我想不到其他办法抱歉*/

        var player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player');
        var joystick = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('joystick');
        var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor');

        player.active = false;
        joystick.active =false;
        inter.active = false;

        if (this.jude == 1) {
            /*关闭player和joyStick */
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "*使用摇杆控制角色移动*"
                }, {
                    role: 1,
                    coentent: "*靠近道具时点击交互按钮即可拾取*"
                }, {
                    role: 1,
                    coentent: "*部分道具再次点击即可交互*"
                }, {
                    role: 1,
                    coentent: "我这是睡了第几天了……Chloe……按惯例，先看看收音机收没收到一些有意义的东西。"
                }]);
        } else if (this.jude == 2) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "等等！这和之前的噪音不一样，点…横…点…点…这似乎是Morse码？！我需要听的更清楚一点——找到最准确的频率。"
                }]);
        } else if (this.jude == 3) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "天哪，这真的好像是morse，难道是Chloe吗？！我得把这些记录下来，头好晕……"
                }, {
                    role: 1,
                    coentent: "*任务清单已经更新*"
                }]);
        } else if (this.jude == 4) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "我想我收拾好东西了，就现在，出发。"
                }, {
                    role: 1,
                    coentent: "*前往房间左下角并交互以出门*"
                }]);
        } else if (this.jude == 5) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "前面好像有一份报纸，是风吹过来的？"
                }, {
                    role: 1,
                    coentent: "*捡起的报纸会自动收入日记本中*"
                }]);
        } else if (this.jude == 6) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "去前面的信号塔吧"
                }]);
        } else if (this.jude == 7) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "大门被一个密码锁锁住了。这里似乎没有别的入口，如果没点提示的话我只能猜了。"
                }]);
        } else if (this.jude == 8) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "我好像还有线索没收集到。"
                }]);
        } else if (this.jude == 9) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "这是---一张纸条！" + "我来看看---这是门的密码吗？我试试。"
                }]);
        } else if (this.jude == 10) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "*（一种植物），门还没有上电，得想办法恢复电力，这附近应该有能操作的地方吧"
                }]);
        } else if (this.jude == 11) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "这个房间真小，是不是得把那边那玩意重新启动一下？"
                }]);
        } else if (this.jude == 12) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "现在应该通电了吧。"
                }]);
        } else if (this.jude == 13) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "我需要整理一下线路来解析信号。"
                }]);
        } else if (this.jude == 14) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "不错，有才，国宝级画师。"
                }]);
        } else if (this.jude == 15) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "这……" + "John" + "开头……坐标……那肯定是Chloe了！我收拾一下就出发。"
                }, {
                    role: 1,
                    coentent: "*点击交互按钮以进行操作*"
                }]);
        } else if (this.jude == 16) {
            this.init([//文本输入区 同时要修改最上面的rolemap 添加图像名称name及图形路径url 图形需要放在resources 的role文件夹中
                {
                    role: 1,
                    coentent: "路很长，不过没关系。"
                }]);
        }


        window.dialog = this.node;




        this.node.on('touchstart',this.touchStart,this);
        /*this.dialog.on(cc.Node.EventType.MOUSE_DOWN, this.onMouseDown, this)*/
        //cc.systemEvent.on("keydown",this.onKeyDown,this);
        
    },


    onDestroy(){
        //cc.systemEvent.off("keydown",this.onKeyDown,this);
        /*this.dialog.off(cc.Node.EventType.MOUSE_UP, this.onMouseDown, this)*/
        this.node.off('touchstart',this.touchStart,this);

    },

    touchStart(){
            this.nextTextDate();
        
    },
    
    init(textDateArr){
        this.nowText =null;
        this.textEnd =true;
        this.tt =0;

        
        this.textIndex = -1;
        this.textDataArr = textDateArr;
        cc.log(textDateArr)
        this.nextTextDate();
    },

    nextTextDate() {
        if (!this.textEnd) return;
        if (++this.textIndex < this.textDataArr.length){
            this.setTextData(this.textDataArr[this.textIndex]);
        }else{
            this.closeDialog();
        }

    },

    setTextData(textData){
        if(!this.textEnd) return;
        this.textEnd = false;

        this.nameLable.string = roleMap[textData.role].name;
        this.textLable.string = "";
        this.nowText = textData.coentent;

        cc.loader.loadRes(roleMap[textData.role].url,cc.SpriteFrame,(err, texture)=> {
            this.icon.spriteFrame =texture;
        });

    },

    closeDialog(){
        this.nowText =null;
        this.textEnd =true;
        this.textIndex = -1;
        this.textLable.string = "";

        var player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player');
        var joystick = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('joystick');
        var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor');

        // cc.log("over");
         /*重新启动player和joyStick */
         player.active = true;
         joystick.active =true;
         inter.active = true;
        this.dialog.active = false;/*结束关闭dialog*/ 
    },
    
    update (dt) {
        if(!this.nowText) return;
        this.tt +=dt;

        if(this.tt >=0.1){
            if(this.textLable.string.length < this.nowText.length){
                this.textLable.string =this.nowText.slice(0, this.textLable.string.length + 1);
        } else {
            this.textEnd = true;
            this.nowText = null;
        }
        this.tt = 0;
        }
    },
});
