cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('newspaper_father');
        if(newspaper_father.children[0].name == "newspaper4"){
            cc.director.getScene().getChildByName('dialog').active = false;
        }
        cc.director.getScene().getChildByName('electricBox').active = false;
        cc.director.getScene().getChildByName('dialog1').active = false;
        cc.director.getScene().getChildByName('radio').active = false;
    },

    start () {

    },

    // update (dt) {},
});
