// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {

    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.game.removePersistRootNode(cc.director.getScene().getChildByName("Canvas"));
    },

    start () {
    },

    update (dt) {
        if(cc.isValid(cc.director.getScene().getChildByName("Canvas"))){
            var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor').getComponent(cc.Button);
            var child = cc.director.getScene().children;
            var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
            var Pos_goal = cc.director.getScene().getChildByName('goal').getPosition();
            if(Pos_player.x <= Pos_goal.x + 500 && Pos_player.x >= Pos_goal.x - 500 && Pos_player.y <= Pos_goal.y + 500 && Pos_player.y >= Pos_goal.y - 500 ) {
                inter.interactable = true;
            }
            else inter.interactable = false;
        }
        else return;
    },
});
