function getStatus (event) {
    switch (event) {
        case cc.VideoPlayer.EventType.PLAYING:
            return 'PLAYING';
        case cc.VideoPlayer.EventType.PAUSED:
            return 'PAUSED';
        case cc.VideoPlayer.EventType.STOPPED:
            return 'STOPPED';
        case cc.VideoPlayer.EventType.COMPLETED:
            return 'COMPLETED';
        case cc.VideoPlayer.EventType.META_LOADED:
            return 'META_LOADED';
        case cc.VideoPlayer.EventType.CLICKED:
            return 'CLICKED';
        case cc.VideoPlayer.EventType.READY_TO_PLAY:
            return 'READY_TO_PLAY';
        default:
            return 'NONE';
    }
};
cc.Class({
    extends: cc.Component,

    properties: {
        videoPlayer: cc.VideoPlayer,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () { this.videoPlayer.node.active = false;},
    onVideoPlayerEvent (sender, event) {
        cc.log(getStatus(event));
        if (event === cc.VideoPlayer.EventType.CLICKED) {
            if (this.videoPlayer.isPlaying()) {
                cc.director.loadScene('scene1');
            } else {
                this.videoPlayer.play();
            }
        }
        else if (event === cc.VideoPlayer.EventType.COMPLETED) {
            cc.director.loadScene('scene1');
        }
    },

    toggleVisibility (event) {
        this.videoPlayer.node.active = true;
        // this.playVideoArea.active = this.videoPlayer.node.active;
        // this.visibility.string = 'Visibility: ' + this.videoPlayer.node.active;
    },

    play () {
        this.videoPlayer.play();
        // this.playVideoArea.active = false;
    },

    start () {

    },

    // update (dt) {},
});
