cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

    },

    pressed(){
        var paper = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('纸条');
        if(paper.active == false){
            paper.active = true;
        }
        else if(paper.active == true){
            paper.active = false;
        }
    },
    start () {

    },

    // update (dt) {},
});
