// var flag = 0;
var node = 0;
// Learn cc.Class:
//  - https://docs.cocos.com/creator/manual/en/scripting/class.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    //onLoad () {},
    playSound(){

            if(node.name != "door")
            {
               this.node.getChildByName("itemsound").getComponent(cc.AudioSource).play();
            }
            else this.node.getChildByName("bottomsound").getComponent(cc.AudioSource).play();

    },

    getUI:function(){
        var UI = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI');
        return UI;
    },
    getContent:function(){
        var content = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('itemBar').getChildByName('view').getChildByName('content');
        return content;
    },

    getPrefab:function(){
        if(cc.director.getScene().name == "scene1" ||cc.director.getScene().name == "scene2" ||cc.director.getScene().name == "scene3"){
        //先拿容器，再根据场景名称确定要拿的item
        var content = this.getContent();
        var player = this.node.parent.parent.getChildByName('player');
        var Pos_player = this.node.parent.parent.getChildByName('player').getPosition();
        var UI = this.getUI();
        var diary_father = UI.getChildByName("diary_father");
        var newspaper_father = UI.getChildByName("newspaper_father");
        if(node.name == "door"){
            if(cc.director.getScene().name == "scene1" && diary_father.children[0].name != "diary3"){
                cc.director.getScene().getChildByName('dialog4').active = true;
            }
            else if(cc.director.getScene().name == "scene1" && diary_father.children[0].name == "diary3"){
                cc.director.loadScene('scene2');
            }
            if(cc.director.getScene().name == "scene2"){cc.director.loadScene('scene3');}
        }
        else if(node.name == "exit"){
            cc.director.loadScene('scene22');
        }
        else {
            if(node.name == "radio"){
                cc.director.getScene().getChildByName('diary2').active = true;
                diary_father.getChildByName("diary1").destroy();
                cc.director.getScene().getChildByName('diary2').parent = diary_father;
                cc.loader.loadRes(node.name, function (err, prefab) {
                    var newNode = cc.instantiate(prefab);
                    content.addChild(newNode);
                    cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                });}
            else if(node.name == "diary1"){
                cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                cc.director.getScene().getChildByName('diary1').active = true;
                cc.director.getScene().getChildByName('diary1').parent = diary_father;
            }
            else if(node.name == "photo"){
                cc.loader.loadRes(node.name, function (err, prefab) {
                    var newNode = cc.instantiate(prefab);
                    content.addChild(newNode);
                    cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                });}


            else if(node.name == "newspaper"){
                if(cc.director.getScene().name == "scene1"){
                    cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                    cc.director.getScene().getChildByName('newspaper').active = true;
                    cc.director.getScene().getChildByName('newspaper').parent = newspaper_father;
                }
                else if(cc.director.getScene().name == "scene2"){
                    cc.director.getScene().getChildByName('items').getChildByName('newspaper').destroy();
                    cc.director.getScene().getChildByName('newspaperpic').destroy();
                    newspaper_father.getChildByName("newspaper").destroy();
                    cc.director.getScene().getChildByName('newspaper1').active = true;
                    cc.director.getScene().getChildByName('newspaper1').parent = newspaper_father;
                }
                else if(cc.director.getScene().name == "scene3"){
                    newspaper_father.getChildByName("newspaper1").destroy();
                    cc.director.getScene().getChildByName('newspaper').destroy();
                    cc.director.getScene().getChildByName('newspaper2').active = true;
                    cc.director.getScene().getChildByName('newspaper2').parent = newspaper_father;
                }
            }
            else if(node.name == "panel"){
                var panel = cc.director.getScene().getChildByName('items').getChildByName(node.name);
                if(panel.active == false){
                    panel.active = true;
                }
                else if(panel.active == true){
                    panel.active == false;
                }
            }
            else if(node.name == "password"){
                cc.loader.loadRes(node.name, function (err, prefab) {
                    var newNode = cc.instantiate(prefab);
                    content.addChild(newNode);
                    cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                    cc.director.getScene().getChildByName('dialog1').active = true;
                });}
            else {
                cc.loader.loadRes(node.name, function (err, prefab) {
                    var newNode = cc.instantiate(prefab);
                    content.addChild(newNode);
                    cc.director.getScene().getChildByName('items').getChildByName(node.name).destroy();
                });}
        }}
        else if(cc.director.getScene().name === "scene22"){
            if(node.name == "photo1"){
               if( cc.director.getScene().getChildByName('photo1').active == true){
                   cc.director.getScene().getChildByName('photo1').active = false;
               }
               else if( cc.director.getScene().getChildByName('photo1').active == false){
                   cc.director.getScene().getChildByName('photo1').active = true;
               }
               cc.log(cc.director.getScene().getChildByName('photo1').active);
            }
            else if(node.name == "door"){
                cc.director.loadScene('scene3');
            }
        }
        else if(cc.director.getScene().name == "scene4"){
            var UI = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI');
            var diary_father = UI.getChildByName("diary_father");
            if(node.name == "door_control") {
                if (diary_father.children[0].name == "diary4") {
                    //弹出提示需要先去配电室并加入日记
                    cc.director.getScene().getChildByName('dialog').active = true;
                    diary_father.getChildByName("diary4").destroy();
                    cc.director.getScene().getChildByName('diary5').active = true;
                    cc.director.getScene().getChildByName('diary5').parent = diary_father;
                }
                if (diary_father.children[0].name == "diary6") {
                    //可以进去了
                    cc.director.getScene().getChildByName('map').getChildByName("关门").getComponent(cc.AudioSource).play();
                    cc.director.loadScene('control');
                }
            }
            else if (node.name == "newspaper"){
                var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("newspaper_father");
                newspaper_father.getChildByName("newspaper2").destroy();
                cc.director.getScene().getChildByName('newspaper').destroy();
                cc.director.getScene().getChildByName('newspaper3').active = true;
                cc.director.getScene().getChildByName('newspaper3').parent = newspaper_father;
            }
            else if(node.name == "door_electric"){
                //直接切换场景到配电室
                cc.director.getScene().getChildByName('map').getChildByName("关门").getComponent(cc.AudioSource).play();
                cc.director.loadScene('electric');
            }
        }
        else if(cc.director.getScene().name == "electric"){

            if(node.name == "door"){
                //回到场景4
                cc.director.getScene().getChildByName('map').getChildByName("关门").getComponent(cc.AudioSource).play();
                cc.director.loadScene('scene4');
            }
            else if(node.name == "newspaper"){
                var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("newspaper_father");
                //如果没捡场景4的报纸
                if(newspaper_father.children[0].name == "newspaper2"){
                    newspaper_father.getChildByName("newspaper2").destroy();
                    cc.director.getScene().getChildByName('newspaper').destroy();
                    cc.director.getScene().getChildByName('newspaper4').active = true;
                    cc.director.getScene().getChildByName('newspaper4').parent = newspaper_father;
                }
                //如果捡了场景4的报纸
                else if(newspaper_father.children[0].name == "newspaper3"){
                    newspaper_father.getChildByName("newspaper3").destroy();
                    cc.director.getScene().getChildByName('newspaper').destroy();
                    cc.director.getScene().getChildByName('newspaper4').active = true;
                    cc.director.getScene().getChildByName('newspaper4').parent = newspaper_father;
                }
            }
            else if(node.name == "ebox"){
                //打开电力箱
                cc.director.getScene().getChildByName('electricBox').getChildByName("closebox").getComponent(cc.AudioSource).play();
                if(cc.director.getScene().getChildByName('electricBox').active == false ){
                    cc.director.getScene().getChildByName('electricBox').active = true;
                }
                else cc.director.getScene().getChildByName('electricBox').active = false;
            }
            else if(node.name == "hint"){
                cc.loader.loadRes(node.name, function (err, prefab) {
                    var newNode = cc.instantiate(prefab);
                    newNode.parent = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('itemBar').getChildByName('view').getChildByName('content');
                    cc.director.getScene().getChildByName(node.name).destroy();
                });
            }
        }
        else if(cc.director.getScene().name == "control"){
            var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
            if( cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father').children[0].name == "diary7"){
                cc.director.loadScene('final');
            }
            else if(cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father').children[0].name != "diary7"){
                if(cc.director.getScene().getChildByName('circuit').active == true){
                    cc.director.getScene().getChildByName('circuit').active = false;
                }
                else if(cc.director.getScene().getChildByName('circuit').active == false){
                    cc.director.getScene().getChildByName('circuit').active = true;
                }
            }
        }
        else if(cc.director.getScene().name == "final"){
            cc.director.getScene().getChildByName('Canvas').destroy();
            cc.director.loadScene('final1');
        }
    },

    start () {
    },
    update (dt) {
        //场景一
        // var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
        // cc.log(Pos_player);
         if(cc.director.getScene().name === "scene1" || cc.director.getScene().name === "scene2" ){
            var Pos_player = this.node.parent.parent.getChildByName('player').getPosition();
            // cc.log(Pos_player.x,Pos_player.y);
            var items = cc.director.getScene().getChildByName('items').children;
            var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor').getComponent(cc.Button);
            if(items.length != 0){
                for (let i = 0; i < items.length; i++) {
                    var Pos_item = items[i].getPosition();
                    if(Pos_player.x <= Pos_item.x + 100 && Pos_player.x >= Pos_item.x - 100 && Pos_player.y <= Pos_item.y + 100 && Pos_player.y >= Pos_item.y - 100)
                    {
                        node = items[i];
                        cc.log("stand by",node.name);
                        inter.interactable = true;
                        break;
                    }
                    else inter.interactable = false;
                }
            }
            else inter.interactable = false;}

       //场景三
       else if(cc.director.getScene().name === "scene3"){
           var interactor = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("interactor").getComponent(cc.Button);
           var Pos_player = this.node.parent.parent.getChildByName('player').getPosition();
           var Pos_exit = cc.director.getScene().getChildByName('exit');
           if(cc.isValid(cc.director.getScene().getChildByName('newspaper'))){
               var Pos_paper = cc.director.getScene().getChildByName('newspaper').getPosition();
           }
           if(Pos_player.x <= Pos_exit.x + 200 && Pos_player.x >= Pos_exit.x - 200 && Pos_player.y <= Pos_exit.y + 200 && Pos_player.y >= Pos_exit.y - 200 ) {
               interactor.interactable = true;
               node.name = "exit";
           }
           else if(cc.isValid(cc.director.getScene().getChildByName('newspaper')) && Pos_player.x <= Pos_paper.x + 200 && Pos_player.x >= Pos_paper.x - 200 && Pos_player.y <= Pos_paper.y + 200 && Pos_player.y >= Pos_paper.y - 200){
               node.name = "newspaper";
               interactor.interactable = true;
           }
           else if(cc.director.getScene().getChildByName('items').children.length != 0 ){
               var child = cc.director.getScene().getChildByName('items').children[0].getPosition();
               if(Pos_player.x <= child.x + 200 && Pos_player.x >= child.x - 200 && Pos_player.y <= child.y + 200 && Pos_player.y >= child.y - 200 ) {
                   interactor.interactable = true;
                   node.name = "password";
               }
               else interactor.interactable = false;
           }

               else interactor.interactable = false;
       }
       else if(cc.director.getScene().name === "scene22"){
           var interactor = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("interactor").getComponent(cc.Button);
           var Pos_player = this.node.parent.parent.getChildByName('player').getPosition();
           var Pos_door = cc.director.getScene().getChildByName('door').getPosition();
           var Pos_photo = cc.director.getScene().getChildByName('photo').getPosition();
           if(Pos_player.x <= Pos_photo.x + 200 && Pos_player.x >= Pos_photo.x - 200 && Pos_player.y <= Pos_photo.y + 200 && Pos_player.y >= Pos_photo.y - 200 ) {
               interactor.interactable = true;
               node.name = "photo1";
           }
           else if(Pos_player.x <= Pos_door.x + 200 && Pos_player.x >= Pos_door.x - 200 && Pos_player.y <= Pos_door.y + 200 && Pos_player.y >= Pos_door.y - 200 ) {
               interactor.interactable = true;
               node.name = "door";
           }
           else interactor.interactable = false;
       }

        else if(cc.director.getScene().name === "scene4") {
             var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('newspaper_father');
             if(newspaper_father.children[0].name == "newspaper4" && cc.isValid(cc.director.getScene().getChildByName('newspaper'))){
                 cc.director.getScene().getChildByName('newspaper').destroy();
             }
             var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
             if(cc.isValid(cc.director.getScene().getChildByName('newspaper'))){
                var Pos_paper = cc.director.getScene().getChildByName('newspaper').getPosition();
            }
            var interactor = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("interactor").getComponent(cc.Button);
            var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
            var Pos_control = cc.director.getScene().getChildByName('door_control').getPosition();
            var Pos_electric = cc.director.getScene().getChildByName('door_electric').getPosition();
            if(Pos_player.x <= Pos_control.x + 200 && Pos_player.x >= Pos_control.x - 200 && Pos_player.y <= Pos_control.y + 200 && Pos_player.y >= Pos_control.y - 200 ) {
                interactor.interactable = true;
                node.name = "door_control";
            }
            else if(cc.isValid(cc.director.getScene().getChildByName('newspaper')) && Pos_player.x <= Pos_paper.x + 200 && Pos_player.x >= Pos_paper.x - 200 && Pos_player.y <= Pos_paper.y + 200 && Pos_player.y >= Pos_paper.y - 200 ) {
                interactor.interactable = true;
                node.name = "newspaper";
            }
            else if(Pos_player.x <= Pos_electric.x + 200 && Pos_player.x >= Pos_electric.x - 200 && Pos_player.y <= Pos_electric.y + 200 && Pos_player.y >= Pos_electric.y - 200 ) {
                interactor.interactable = true;
                node.name = "door_electric";
            }
            else  interactor.interactable = false;
        }
        else if(cc.director.getScene().name == "electric"){
             var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('newspaper_father');
             if(newspaper_father.children[0].name == "newspaper4" && cc.isValid(cc.director.getScene().getChildByName('newspaper'))){
                 cc.director.getScene().getChildByName('newspaper').destroy();
             }
             var content = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('itemBar').getChildByName('view').getChildByName('content');
             if( cc.isValid(content.getChildByName("hint")) && cc.isValid(cc.director.getScene().getChildByName('hint'))){
                 cc.director.getScene().getChildByName('hint').destroy();
             }
             if(cc.isValid(cc.director.getScene().getChildByName('newspaper'))){
                 var Pos_paper = cc.director.getScene().getChildByName('newspaper').getPosition();
             }
            if(cc.isValid(cc.director.getScene().getChildByName('hint'))){
                var hint = cc.director.getScene().getChildByName('hint').getPosition();
            }
            var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('interactor').getComponent(cc.Button);
            var Pos_player = this.node.parent.parent.getChildByName('player').getPosition();
            var Pos_box = cc.director.getScene().getChildByName('ebox').getPosition();
            var Pos_door = cc.director.getScene().getChildByName('door').getPosition();
            if(Pos_player.x <= Pos_box.x + 100 && Pos_player.x >= Pos_box.x - 100 && Pos_player.y <= Pos_box.y + 100 && Pos_player.y >= Pos_box.y - 100 ) {
                inter.interactable = true;
                node.name = "ebox";
            }
            else if(cc.isValid(cc.director.getScene().getChildByName('newspaper')) && Pos_player.x <= Pos_paper.x + 200 && Pos_player.x >= Pos_paper.x - 200 && Pos_player.y <= Pos_paper.y + 200 && Pos_player.y >= Pos_paper.y - 200){
                node.name = "newspaper";
                inter.interactable = true;
            }
            else if(Pos_player.x <= Pos_door.x + 100 && Pos_player.x >= Pos_door.x - 100 && Pos_player.y <= Pos_door.y + 100 && Pos_player.y >= Pos_door.y - 100 ) {
                inter.interactable = true;
                node.name = "door";
        }
            else if(cc.isValid(cc.director.getScene().getChildByName('hint')) && Pos_player.x <= hint.x + 100 && Pos_player.x >= hint.x - 100 && Pos_player.y <= hint.y + 100 && Pos_player.y >= hint.y - 100 ) {
                inter.interactable = true;
                node.name = "hint";
            }
            else  {
                //离开电力箱时电力箱节点关闭
                inter.interactable = false;
                cc.director.getScene().getChildByName('electricBox').active = false;
            }
        }
        },
});
