// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
       cable1:cc.Node,
       cable2:cc.Node,
       lock:cc.Sprite,
       textureUnlock:cc.SpriteFrame,
       textureLock:cc.SpriteFrame,
    },
    onLoad(){
        cc.director.getScene().getChildByName('dialog').active = true;
    },
    update(dt){
        var self = this;
        var inter = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName("interactor").getComponent(cc.Button);
        var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
        if(this.cable1.opacity == 255 && this.cable2.opacity == 255){
                self.lock.spriteFrame =this.textureUnlock;
                /* 解锁音效，出口 */
            if(diary_father.children[0].name == "diary6"){
                diary_father.getChildByName("diary6").destroy();
                cc.director.getScene().getChildByName('diary7').active = true;
                cc.director.getScene().getChildByName('diary7').parent = diary_father;
                cc.director.getScene().getChildByName('dialog1').active = true;}
           else return;
            }
        else{
            inter.interactable = false;
            self.lock.spriteFrame =this.textureLock;
        }
    }
});
