cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        if(cc.director.getScene().name == "scene2"){cc.director.getScene().getChildByName('dialog_father').active = false;}
    },

    start () {

    },

    update (dt) {
        var Pos_player = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('player').getPosition();
        this.node.position = this.node.parent.convertToNodeSpaceAR(Pos_player);
    },
});
