cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.active = false;
    },

    wear(){
        if(this.node.active = false){
            this.node.active = true;
        }
        else if(this.node.active = true){
            this.node.active = false;
        }
    },

    start () {

    },

    // update (dt) {},
});
