cc.Class({
    extends: cc.Component,

    properties: {
        cable1:cc.Node,
       cable2:cc.Node,
       btn:cc.Node,
    },

    update(){
        if(this.cable1.opacity ==255 && this.cable2.opacity ==255){
            this.btn.active = true;
            this.node.active = false;
        }
    }
});
