cc.Class({
    extends: cc.Component,

    properties: {
     light1:cc.Sprite,
     light2:cc.Sprite,
     light3:cc.Sprite,
     light4:cc.Sprite,
     light5:cc.Sprite,
        openSound: {
            type: cc.AudioClip,
            default: null,
        },

    },

    onLoad(){
        // var newspaper_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('newspaper_father');
        this.judge = true;
        // cc.log(newspaper_father.children[0].name);

    },
    update(){
        var self = this;
        if(self.light1.node.opacity == 255 && self.light2.node.opacity == 255 && self.light3.node.opacity == 255 &&
            self.light4.node.opacity == 255 &&self.light5.node.opacity == 255 ){
                // if(this.judge){
                //  cc.audioEngine.playEffect(self.openSound, false,1);
                //  this.jude = false;
                // }
            var diary_father = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('diary_father');
            if(diary_father.children[0].name == "diary5"){
                cc.director.getScene().getChildByName('electricBox').getChildByName('sound').getComponent(cc.AudioSource).play();
               diary_father.getChildByName("diary5").destroy();
                cc.director.getScene().getChildByName('diary6').active = true;
                cc.director.getScene().getChildByName('diary6').parent = diary_father;
            cc.director.getScene().getChildByName('dialog1').active = true;
            }
            else if(diary_father.children[0].name == "diary4"){
                cc.director.getScene().getChildByName('electricBox').getChildByName('sound').getComponent(cc.AudioSource).play();
                diary_father.getChildByName("diary4").destroy();
                cc.director.getScene().getChildByName('diary6').active = true;
                cc.director.getScene().getChildByName('diary6').parent = diary_father;
                cc.director.getScene().getChildByName('dialog1').active = true;
            }
            else cc.log("completed");
    }
        },
});


