cc.Class({
    extends: cc.Component,

    properties: {
        btn1:cc.Node,
      //   btn2:cc.Node,
        targetbtn1Dir1:cc.Integer,
        targetbtn1Dir2:cc.Integer,
      //   targetbtn2Dir1:cc.Integer,
      //   targetbtn2Dir2:cc.Integer,
            connect1:cc.Node,
      // connect2:cc.Node,

    },

   update(dt){

      
      let btn1Dir = this.btn1.getComponent('rotate').jude() ;
      // let btn2Dir = this.btn2.getComponent('rotate').jude() ;
      this.node.opacity=50;
      if( this.connect1.opacity == 255){
      if (btn1Dir == this.targetbtn1Dir1 ||  btn1Dir == this.targetbtn1Dir2 ){
        this.node.opacity = 255
      // }}else if( this.connect2.opacity == 255){
      //    if (btn2Dir == this.targetbtn2Dir1 ||  btn2Dir == this.targetbtn2Dir2 ){
      //       {this.node.opacity = 255}
      
   }}
},
   opacity(){
      return this.node.opacity;
   }
});
