cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},
    hint(){
        var pt = cc.director.getScene().getChildByName('Canvas').getChildByName('game').getChildByName('UI').getChildByName('hint');
        if(pt.active == false){
            pt.active = true;
        }
        else if(pt.active == true){
            pt.active = false;
        }
    },
    start () {

    },

    update (dt) {

    },
});
