cc.Class({
    extends: cc.Component,

    properties: {
     
    },
   onLoad(){
     this.node.angle = parseInt(Math.random()*5)*90;

   },

    rotate(){
      let angle=this.node.angle; 
    
      this.node.angle=angle-90; 
      if(this.node.angle <= 0){this.node.angle = this.node.angle + 360};

        console.log("rotation",this.node.angle/90);
    
    },

    jude(){
      return parseInt( this.node.angle/90);
    }
  
});
