cc.Class({
    extends: cc.Component,

    properties: {
        stick:{
            type:require("joystick"),
            default:null,
        },

        speed:100,

    },


    onLoad () {
    },

    start () {
    },

    update (dt) {
        if(this.stick.dir.mag()<0.5){
            return;
        }

        var vx = this.stick.dir.x * this.speed;
        var vy = this.stick.dir.y * this.speed;

        var sx = vx * dt;
        var sy = vy * dt;
//移动
        this.node.x += sx;
        this.node.y += sy;
//方向计算
//         var r = Math.atan2(this.Rocker.dir.y,this.Rocker.dir.x);
//         var degree = r * 180/(Math.PI);
//         degree = 360 - degree + 90;
//         this.node.rotation = degree;
    },

});